.search--primary {
  position: absolute;
  top: 355px;
  left: 575px;
}

.search--primary {
  height: 30px;
  width: 50px;
}

:root {
  --primary: #a097da;
  --warning: #ffd028;
  --danger: #eb3f27;
  --success: #75fa83;
  --white: #fdfdfd;
  --dark: #181717;
}

.btn {
  font-family: "Prompt", sans-serif;
  font-size: 15px;
  border-radius: 10px;
  cursor: pointer;
  transition: transform 0.3s ease;
  box-shadow: 0 4px 8px 0 grey;
}

.btn:hover {
  transform: translateY(-3px);
  background-color: rgb(221, 199, 231);
}

/* Button Styles */

.btn--primary--solid {
  background-color: #a097da;
  background-color: var(--primary);
  color: #fdfdfd;
  color: var(--white);
  border: 2px solid rgb(142, 120, 223);
}

.btn--primary--outline {
  background-color: transparent;
  color: #a097da;
  color: var(--primary);
  border: 2px solid #a097da;
  border: 2px solid var(--primary);
}

.btn--medium {
  height: 40px;
  font-size: 18px;
}

.Homepage {
  text-align: center;
  font-family: Raleway;
  font-weight: 300;
}

.App-header {
  height: 100px;
  width: auto;
  margin: 0;
  line-height: 50%;
}

.titles {
  height: 100px;
  background: rgb(219, 155, 134);
  background: radial-gradient(
    circle,
    rgb(215, 129, 226) 12%,
    rgba(224, 90, 131, 0.65) 85%
  );
  border: 2px grey;
  box-shadow: 0 4px 8px 0 grey;
  text-align: center;
  padding-top: 2%;
  padding-bottom: 2%;
}

.App-title {
  font-family: Prompt, monospace;
  color: white;
  font-size: 60px;
  text-align: center;
  vertical-align: middle;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke: purple;
}

.sub-title {
  font-family: Raleway, monospace;
  color: white;
  font-size: 15px;
}

.Select-diff {
  border: 2px solid grey;
  border-radius: 25px;
  height: 200px;
  width: 700px;
  box-shadow: 0 4px 8px 0 grey;
  background-color: rgb(214, 187, 206);
}

.Select-diff:hover {
  box-shadow: 0 8px 8px 0 white;
}

.Options {
  color: grey;
  align-content: center;
}

.outer {
  display: table;
  position: absolute;
  top: 100px;
  left: 0;
  height: 100%;
  width: 100%;
}

.middle {
  display: table-cell;
  padding: 0;
  vertical-align: middle;
}

.inner {
  margin-left: auto;
  margin-right: auto;
  width: 700px;
}

.test-visible {
  color: white;
  left: 15%;
  top: 20%;
  position: absolute;
  width: 70%;
  height: 70%;
  background-color: rgb(226, 100, 195);
  opacity: 0.8;
  visibility: visible;
  transform: scale(1);
  transition: visibility 0s linear 0.5, opacity 0.5 0s, transform 0.5;
  z-index: 2;
  text-align: center;
  border-radius: 25px;
}

.test-hidden {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgb(226, 100, 195);
  opacity: 0;
  visibility: hidden;
  transform: scale(1.1);
  transition: visibility 0s linear 0.25s, opacity 0.25s 0s, transform 0.25s;
  z-index: -2;
}

.pop-up {
  font-family: "Prompt";
  font-size: 50px;
}

.namaste {
  background: rgb(226, 100, 195);
  color: white;
  padding: 1.5rem;
  font-family: "Caveat";
  font-size: 80px;
  border-radius: 25px;
}

